import React from 'react'

import { utilService } from '../services/util.service'

import trash from "../assets/img/trash.png"
import edit from "../assets/img/edit.png"
import details from "../assets/img/details.png"
import imgDef from '../assets/img/default.jpg'

import { Link } from 'react-router-dom'
import { userService } from '../services/user.service'

export const ToyPreview = ({ toy, onRemoveToy }) => {

    const { createdAt } = toy
    const date = utilService.dateToString(createdAt)
    const loggedInUser = userService.getLoggedinUser()
    return (
        <section className="toy-container">
            <h2>{toy.name}</h2>
            <h3>${toy.price}</h3>
            <h4>{date}</h4>

            <div className="img-container">
                <img src={toy.src || imgDef} alt="" />
            </div>
            <div className="btns-container">
                {(!loggedInUser?.isAdmin) &&
                    <>

                        <button
                            className="delete-btn" onClick={() => onRemoveToy(toy._id)}>
                            <img src={trash}></img>
                        </button>

                        <Link to={`/toy/edit/${toy._id}`}>
                            <button className="update-btn">
                                <img src={edit}></img>
                            </button>
                        </Link>

                        <Link to={`/toy/details/${toy._id}`}>
                            <button>
                                <img src={details}></img>
                            </button>
                        </Link>
                    </>
                }

            </div>
        </section >
    )
}



