import { storageService } from './async-storage.service'
import { httpService } from './http.service';

export const toyService = {
    query,
    getById,
    save,
    remove,
    getEmptyToy,
}
window.cs = toyService;

const STORAGE_KEY = 'toy'
const TOY_BASE_ENDPOINT = 'toy'

async function query() {
    const toysFromDB = await httpService.get(TOY_BASE_ENDPOINT)
    return toysFromDB
}

async function getById(toyId) {
    const toysFromDB = await httpService.get(`${TOY_BASE_ENDPOINT}/${toyId}`)
    return toysFromDB
}

async function remove(toyId) {
    await storageService.remove(STORAGE_KEY, toyId)
}


async function save(toy) {
    if (toy._id) {
        const savedToy = await httpService.put(TOY_BASE_ENDPOINT, toy)
        return savedToy
    } else {
        try {
            const savedToy = await httpService.post(TOY_BASE_ENDPOINT, toy)
            return savedToy
        } catch (err) {
            console.log(err)
        }
    }
}

function getEmptyToy() {
    return {
        name: '',
        src: '',
        price: '',
        labels: '',
        createdAt: '',
        inStock: ''
    }
}

