import React from "react"
import { StarsRate } from "../cmps/stars-rate.jsx"

export class ReviewForm extends React.Component {

    state = {
        rate: 0,
        content: '',
    }

    focusRef = React.createRef()
    contentRef = React.createRef()

    onChangeTextArea = ({ target }) => {
        const { name } = target
        let value = target.value
        if (name === 'rate') value = +value
        this.setState((prevState) => ({ ...prevState, [name]: value }))
    }

    onSubmit = async (ev) => {
        ev.preventDefault()
        this.props.onAddReview(this.state)
        this.setState({ rate: 0, content: '' })
    }

    onChangeRate = (rate) => {
        this.setState((prevState) => ({ ...prevState, rate }))
    }

    render() {
        return (
            <form className="review-form" onSubmit={this.onSubmit}>
                <StarsRate rate={this.state.rate}
                    onChangeRate={this.onChangeRate}
                />
                <textarea ref={this.focusRef}
                    name="content"
                    id="review-text"
                    value={this.state.content}
                    placeholder="Add your review here"
                    required
                    onChange={this.onChangeTextArea}>
                </textarea>

                <button>Add New Review</button>
            </form>
        )
    }
}