import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import { LogOut } from '../login/logout'
import { onLogout } from '../store/actions/user.actions'

export const AppHeader = () => {

    const { toys } = useSelector((storeState) => storeState.toyModule)
    const { user } = useSelector((storeState) => storeState.userModule)
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const [isOpenModal, setIsOpenModal] = useState(false)
    const [isStore, setIsStore] = useState(true)


    const onToggleModal = () => {
        setIsOpenModal(!isOpenModal)
    }

    const onStorStatToggle = () => {
        setIsOpenModal(false)
        setIsStore(!isStore)
        if (isStore) navigate('/toy/statistics')
        else navigate('/toy')
    }

    const onUserLogout = () => {
        setIsOpenModal(false)
        dispatch(onLogout())
        navigate('/login')
    }

    const logOutBtnStyle =
        !user ? ({ opacity: 0, pointerEvents: 'none' }) : {}


    return (
        <header className="app-header">
            <div className='nav-line-container'>
                <div>
                    <button onClick={onToggleModal} className="logout-btn" style={logOutBtnStyle}>
                        {user ?
                            <button style={{ backgroundImage: 'blue', color: 'white', border: 'none' }} className="user-avatar">
                                {user?.username.charAt(0).toUpperCase()}
                            </button> : []
                        }
                    </button>

                    {isOpenModal && <div className='modal'>
                        {!isStore && <div className='modal-btn' onClick={onStorStatToggle}>
                            <h5>Toys</h5>
                        </div>
                        }
                        <div onClick={onUserLogout}>
                            <LogOut user={user}/>
                        </div>
                    </div>
                    }
                </div>
                <nav>
                    <NavLink to="/">Home</NavLink>
                    <NavLink to="/about">About</NavLink>
                    <NavLink to="/toy">Toys</NavLink>
                </nav>
            </div>
            <div className="header-titles-container">
                <Link
                    className="mister-toy-logo" to="/toy"><h1>Mister Toy</h1>
                </Link>
                <div className="header-inf">
                    <h3>Toys: {toys.length}</h3>
                    <Link
                        className="inf" to="/toy/statistics">Statistical Information
                    </Link>
                </div>
            </div>
        </header>
    )
}

