import React from 'react'
import { Droppable } from 'react-beautiful-dnd'
import { ToyPreview } from './toy-preview'

export function Toylist({ toys, onRemoveToy }) {

    return (
        <section className='toys-list-container'>
                {toys.map(toy =>
                    <ToyPreview
                        key={toy._id}
                        toy={toy}
                        onRemoveToy={onRemoveToy}
                    />
                )}
        </section>

    )
}

